@keyframes videoShow {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    visibility: visible;
  }
}

.vidbg-container {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.vidbg-container video {
  position: absolute;
  margin: 0;
  object-fit: cover;
  top: 50%;
  left: 50%;
  height: 100%;
  transform: translate(-50%, -50%);
  // transition: 0.25s opacity ease-in-out;
  max-width: none;
  opacity: 0;
  visibility: hidden;
  animation: videoShow 3s ease-in-out forwards;


  @media screen and (min-width: 1025px) {
    width: 100%;
    height: 100%;
  }
}

.vidbg-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
